html {
  --main-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-item {
  justify-content: center;
  margin-right: 20px;
  list-style: none;
  display: inline-block;
}

.nav-item:last-of-type {
  margin-right: 0;
}

.nav-link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav-link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  border: 1% solid var(--main-color);
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
